<template>
  <div class="leverlist">
    <ao-nav :ao-style="{'background': '#1b2945'}"></ao-nav>
    <div class="box">
      <!-- nav -->
      <div class="lever-nav">
        <div class="lever-nav-item" :class="[index === navIndex ? 'active' : '']" v-for="(item, index) in nav" :key="index" @click="change(index)">{{ item }}</div>
      </div>
      <list-table-0 v-if="navIndex === 0" :currency="currency"></list-table-0>
      <list-table-1 v-else-if="navIndex === 1" :currency="currency"></list-table-1>
      <!-- <list-table-2 v-else-if="navIndex === 2" :currency="currency"></list-table-2> -->
      <list-table-3 v-else-if="navIndex === 2" :currency="currency"></list-table-3>
      <list-table-4 v-else></list-table-4>
    </div>
  </div>
</template>

<script>
import AoNav from "@/components/AoNav";
import ListTable0 from './list/ListTable0'
import ListTable1 from './list/ListTable1'
import ListTable2 from './list/ListTable2'
import ListTable3 from './list/ListTable3'
import ListTable4 from './list/ListTable4'

import { getPositionList, cancalPosition, closePosition } from "@/api/asset";

export default {
  components: {
    AoNav,
    ListTable0,
    ListTable1,
    ListTable2,
    ListTable3,
    ListTable4
  },
  data() {
    return {
      navIndex: 0,
      nav: ['挂单中', '交易中','已平仓', '已撤单'],
      currency: localStorage.getItem('web-currency')
    };
  },
  mounted() {
    if(!this.currency) this.$router.back(-1)
  },
  methods: {
    change(index){
      this.navIndex = index
    }
  }
};
</script>

<style  lang="scss">
.leverlist {
  div {
    box-sizing: border-box;
  }

  .box {
    min-height: 500px;
    background: #1a243b;
    width: 97%;
    margin: 30px auto;
    padding: 30px;
  }
  .lever-nav {
    display: flex;
    .lever-nav-item {
      padding-bottom: 10px;
      margin-right: 20px;
      cursor: pointer;
      color: #c7cce6;
      &.active {
        color: #7a98f7;
        border-bottom: 1px solid #7a98f7;
      }
    }
  }
  .th,
  .tr {
    display: flex;
    padding: 8px 0;
    color: #637085;
  }
  .th {
    border-bottom: 1px solid #2e333c;
  }
  .td {
    flex: 1;
  }
  .td-2 {
    flex: 2;
  }
  .th .td,
  .tr .td {
    color: #637085;
    font-size: 14px;
    text-align: center;
  }
  .btn {
    border-radius: 3px;
    color: #fff;
    background-color: #638bd4;
    cursor: pointer;
    font-size: 14px;
    border: none;
    padding: 0 5px;
    line-height: 33px;
    text-align: center;
  }
}
</style>



