<template>
  <div class="leverlist-table-1">
    <div class="table">
      <div class="th">
        <td class="td">类型</td>
        <td class="td">开仓价</td>
        <td class="td">当前价</td>
        <td class="td">保证金</td>
        <td class="td">止盈价</td>
        <td class="td">止损价</td>
        <td class="td">开仓时间</td>
        <td class="td">手续费</td>
        <td class="td">隔夜费</td>
        <td class="td">盈亏</td>
        <td class="td">操作</td>
      </div>
      <div class="tr" v-for="(item, index) in list" :key="index">
        <td class="td">{{ item.type_text }}</td>
        <td class="td">{{ item.open_price }}</td>
        <td class="td">{{ item.price }}</td>
        <td class="td">{{ item.margin }}</td>
        <td class="td">{{ item.stop_profit }}</td>
        <td class="td">{{ item.stop_loss }}</td>
        <td class="td">{{ item.open_time }}</td>
        <td class="td">{{ item.fee }}</td>
        <td class="td">{{ item.overnight_fee }}</td>
        <td class="td">{{ item.profit_loss }}</td>
        <td class="td">
          <span v-if="item.p_s == 0" class="btn" @click="position(index)">平仓</span>
          <span v-else class="text">平仓中</span>
        </td>
      </div>
    </div>
    <div class="no-data">{{ list.length === 0 ? '暂无数据' : '' }}</div>
  </div>
</template>

<script>
import { getPositionList, closePosition } from "@/api/asset";
var __timer__ = null;
export default {
  props: {
    currency: String
  },
  data() {
    return {
      list: [],
      all_profit_loss: "0",
      hazard_rate: "0"
    };
  },
  mounted() {
    __timer__ && clearInterval(__timer__);
    if (!this.currency) this.$router.back(-1);
    this.getPositionList();
    __timer__ = setInterval(() => {
      this.getPositionList();
    }, 5000);
  },
  destroyed() {
    __timer__ && clearInterval(__timer__);
  },
  methods: {
    ffff(v = 0, l = 4) {
      return parseFloat(v).toFixed(l);
    },
    getPositionList() {
      var data = {
        currency: this.currency,
        is_pl_r: 0,
        p_s: 1
      };
      getPositionList(data).then(res => {
        this.all_profit_loss = res.all_profit_loss;
        this.hazard_rate = res.hazard_rate;
        this.list = res.list;
      });
    },
    position(index) {
      var data = { id: index === -1 ? -1 : this.list[index].id };
      if (index === -1) {
        data.currency = this.currency;
      }
      closePosition(data).then(res => {
        index > -1
          ? (this.list[index].p_s = 1)
          : (this.list = this.list.map(o => {
              o.p_s = 1;
              return o;
            }));
      });
    }
  }
};
</script>

<style  lang="scss">
.leverlist-table-1 {
  div {
    box-sizing: border-box;
  }
  .th,
  .tr {
    display: flex;
    padding: 8px 0;
    color: #637085;
  }
  .th {
    border-bottom: 1px solid #2e333c;
  }
  .td {
    flex: 1;
  }
  .td-2 {
    flex: 2;
  }
  .th .td,
  .tr .td {
    color: #637085;
    font-size: 14px;
    text-align: center;
  }
  .btn,
  .text {
    border-radius: 3px;
    color: #fff;
    cursor: pointer;
    font-size: 14px;
    border: none;
    padding: 0 5px;
    line-height: 33px;
    text-align: center;
  }
  .btn {
    background-color: #638bd4;
  }
  .no-data {
    color: #999;
    font-size: 14px;
    text-align: center;
    margin-top: 16px;
  }
}
</style>



