<template>
  <div class="leverlist-table-1">
    <div class="table">
      <div class="th">
         <td class="td" v-for="item in lt_3_td" :key="item">{{ item }}</td>
        <!-- <td class="td">类型</td>
        <td class="td">手数</td>
        <td class="td">持仓价</td>
        <td class="td">平仓价</td>
        <td class="td">止盈价</td>
        <td class="td">止损价</td>
        <td class="td">保证金</td>
        <td class="td">可用保证金</td>
        <td class="td">开仓时间</td>
        <td class="td">平仓时间</td>
        <td class="td">手续费</td>
        <td class="td">隔夜费</td>
        <td class="td">盈亏</td>
        <td class="td">操作</td> -->
      </div>
      <div class="tr" v-for="(item, index) in list" :key="index">
        <td class="td">{{ item.type_text }}</td>
        <td class="td">{{ item.lot_num }}</td>
        <td class="td">{{ ffff(item.position_price, 4) }}</td>
        <td class="td">{{ ffff(item.close_out_price, 4) }}</td>
        <td class="td">{{ ffff(item.stop_profit) }}</td>
        <td class="td">{{ ffff(item.stop_loss) }}</td>
        <td class="td">{{ ffff(item.margin, 4) }}</td>
        <td class="td">{{ ffff(item.margin - item.overnight_fee, 4) }}</td>
        <td class="td">{{ item.open_time }}</td>
        <td class="td">{{ item.close_out_time }}</td>

        <td class="td">{{ ffff(item.close_out_fee) }}</td>
        <td class="td">{{ ffff(item.overnight_fee) }}</td>
        <td class="td" :class="[item.profit_loss < 0 ? 'red' :'green']">{{ ffff(item.profit_loss) }}</td>
        <td class="td">
          <span class="text">{{ item.is_blow_text || lt_3_txt[item.is_blow] }}</span>
        </td>
      </div>
    </div>
    <div class="no-data" v-if="list.length > 0">{{ list.length === 0 ? node_data : '' }}</div>
  </div>
</template>

<script>
import { getClosePositionList, closePosition } from "@/api/asset";
var __timer__ = null;
export default {
  props: {
    currency: String
  },
  data() {
    return {
      lt_3_txt: this.$t('deal.lt_3_txt'), // ["平仓中"],
      lt_3_td: this.$t('deal.lt_3_td'), // [
      node_data: this.$t('deal.node_data'), // "暂无数据",
      list: [],
      all_profit_loss: "0",
      hazard_rate: "0"
    };
  },
  mounted() {
    __timer__ && clearInterval(__timer__);
    if (!this.currency) this.$router.back(-1);
    this.getClosePositionList();
    __timer__ = setInterval(() => {
      this.getClosePositionList();
    }, 5000);
  },
  destroyed() {
    __timer__ && clearInterval(__timer__);
  },
  methods: {
    ffff(v = 0, l = 4) {
      return parseFloat(v).toFixed(l);
    },
    getClosePositionList() {
      var data = {
        currency: this.currency,
        is_pl_r: 0,
        p_s: 1
      };
      getClosePositionList(data).then(res => {
        this.all_profit_loss = res.all_profit_loss;
        this.hazard_rate = res.hazard_rate;
        this.list = res.list;
      });
    },
    position(index) {
      var data = { id: index === -1 ? -1 : this.list[index].id };
      if (index === -1) {
        data.currency = this.currency;
      }
      closePosition(data).then(res => {
        index > -1
          ? (this.list[index].p_s = 1)
          : (this.list = this.list.map(o => {
              o.p_s = 1;
              return o;
            }));
      });
    }
  }
};
</script>

<style  lang="scss">
.leverlist-table-1 {
  div {
    box-sizing: border-box;
  }
  .th,
  .tr {
    display: flex;
    padding: 8px 0;
    color: #637085;
  }
  .th {
    border-bottom: 1px solid #2e333c;
  }
  .td {
    flex: 1;
  }
  .td-2 {
    flex: 2;
  }
  .th .td,
  .tr .td {
    color: #637085;
    font-size: 14px;
    text-align: center;
    &.green {
      color: #0d8551;
    }
    &.red {
      color: #cc4951;
    }
  }
  .text {
    border-radius: 3px;
    color: #fff;
    cursor: pointer;
    font-size: 14px;
    border: none;
    padding: 0 5px;
    line-height: 33px;
    text-align: center;
  }
  .no-data {
    color: #999;
    font-size: 14px;
    text-align: center;
    margin-top: 16px;
  }
}
</style>



