<template>
  <div class="leverlist-table-0">
    <div class="table">
      <div class="th">
        <td class="td" v-for="item in lt_0_td" :key="item">{{ item }}</td>
        <!-- <td class="td">用户名称</td>
        <td class="td">联系方式</td>
        <td class="td">币种</td>
        <td class="td">数量</td>
        <td class="td">当前价</td>
        <td class="td">手续费</td>
        <td class="td">操作</td>-->
      </div>
      <div class="tr" v-for="(item, index) in list" :key="index">
        <td class="td">{{ item.phone }}</td>
        <td class="td">{{ item.currency }}</td>
        <td class="td">{{ ffff(item.amount) }}</td>
        <td class="td">{{ ffff(item.price) }}</td>
        <td class="td">{{ ffff(item.fee) }}</td>
        <td class="td">
          <span class="text">{{ lt_4_txt[0] }}</span>
        </td>
      </div>
    </div>
    <div class="no-data">{{ list.length === 0 ? node_data : '' }}</div>
  </div>
</template>

<script>
import AoNav from "@/components/AoNav";
import { getDealList } from "@/api/user";
import { orderCancel } from "@/api/asset";

export default {
  components: {
    AoNav
  },
  props: {
    currency: String
  },
  data() {
    return {
      lt_0_td: this.$t("deal.lt_0_td"),
      lt_4_txt: this.$t("deal.lt_4_txt"),
      node_data: this.$t("deal.node_data"), // "暂无数据",
      list: [],
      page: 1
    };
  },
  mounted() {
    this.getDealList();
  },
  methods: {
    ffff(v = 0, l = 4) {
      return parseFloat(v).toFixed(l);
    },
    getDealList() {
      var data = {
        page: this.page,
        deal_type: 2,
        status: 2
      };
      getDealList(data).then(res => {
        this.list = this.list.concat(res.list);
      });
    }
  }
};
</script>

<style  lang="scss">
.leverlist-table-0 {
  .th,
  .tr {
    display: flex;
    padding: 8px 0;
    color: #637085;
  }
  .th {
    border-bottom: 1px solid #2e333c;
  }
  .td {
    flex: 1;
  }
  .td-2 {
    flex: 2;
  }
  .th .td,
  .tr .td {
    color: #637085;
    font-size: 14px;
    text-align: center;
  }
  .text {
    border-radius: 3px;
    color: #fff;
    cursor: pointer;
    font-size: 14px;
    border: none;
    padding: 0 5px;
    line-height: 33px;
    text-align: center;
  }
  .no-data {
    color: #999;
    font-size: 14px;
    text-align: center;
    margin-top: 16px;
  }
}
</style>



