<template>
  <div class="leverlist-table-1">
    <div class="table">
      <div class="th">
        <td class="td" v-for="item in lt_1_td" :key="item">{{ item }}</td>
        <!-- <td class="td">类型</td>
        <td class="td">开仓价</td>
        <td class="td">当前价</td>
        <td class="td">保证金</td>
        <td class="td">止盈价</td>
        <td class="td">止损价</td>
        <td class="td">开仓时间</td>
        <td class="td">手续费</td>
        <td class="td">隔夜费</td>
        <td class="td">盈亏</td>  平仓 开仓价  平仓价  保证金  可用保证金 
        <td class="td">操作</td>-->
      </div>
      <div class="tr" v-for="(item, index) in list" :key="index">
        <td class="td">{{ item.type_text }}</td>
        <td class="td">{{ ffff(item.position_price) }}</td>
        <td class="td">{{ ffff(item.price) }}</td>
        <td class="td">{{ ffff(item.margin) }}</td>
        <td class="td">{{ ffff(item.stop_profit) }}</td>
        <td class="td">{{ ffff(item.stop_loss) }}</td>
        <td class="td">{{ item.open_time }}</td>
        <td class="td">{{ ffff(item.open_fee) }}</td>
        <td class="td">{{ ffff(item.overnight_fee) }}</td>
        <td class="td">{{ ffff(item.profit_loss) }}</td>
        <td class="td">
          <span v-if="item.p_s == 0" class="btn" @click="position(index)">{{ lt_1_btn[0] }}</span>
          <span v-else class="text">{{ lt_1_txt[1] }}</span>
        </td>
      </div>
    </div>
    <div class="no-data">{{ list.length === 0 ? node_data : '' }}</div>
  </div>
</template>

<script>
import { getPositionList, closePosition } from "@/api/asset";
var __timer__ = null;
export default {
  props: {
    currency: String
  },
  data() {
    return {
      lt_1_btn: this.$t("deal.lt_1_btn"), // ["平仓"],
      lt_1_txt: this.$t("deal.lt_1_txt"), // ["平仓中"],
      lt_1_td: this.$t("deal.lt_1_td"), // [
      //   "类型",
      //   "开仓价",
      //   "当前价",
      //   "保证金",
      //   "止盈价",
      //   "止损价",
      //   "开仓时间",
      //   "手续费",
      //   "隔夜费",
      //   "盈亏",
      //   "操作"
      // ],
      node_data: this.$t("deal.node_data"), // "暂无数据",
      list: [],
      all_profit_loss: "0",
      hazard_rate: "0"
    };
  },
  mounted() {
    __timer__ && clearInterval(__timer__);
    if (!this.currency) this.$router.back(-1);
    this.getPositionList();
    __timer__ = setInterval(() => {
      this.getPositionList();
    }, 5000);
  },
  destroyed() {
    __timer__ && clearInterval(__timer__);
  },
  methods: {
    ffff(v = 0, l = 4) {
      return parseFloat(v).toFixed(l);
    },
    getPositionList() {
      var data = {
        currency: this.currency,
        is_pl_r: 0,
        p_s: 0
      };
      getPositionList(data).then(res => {
        this.all_profit_loss = res.all_profit_loss;
        this.hazard_rate = res.hazard_rate;
        this.list = res.list;
      });
    },
    position(index) {
      var data = { id: index === -1 ? -1 : this.list[index].id };
      if (index === -1) {
        data.currency = this.currency;
      }
      closePosition(data).then(res => {
        index > -1
          ? (this.list[index].p_s = 1)
          : (this.list = this.list.map(o => {
              o.p_s = 1;
              return o;
            }));
      });
    }
  }
};
</script>

<style  lang="scss">
.leverlist-table-1 {
  div {
    box-sizing: border-box;
  }
  .th,
  .tr {
    display: flex;
    padding: 8px 0;
    color: #637085;
  }
  .th {
    border-bottom: 1px solid #2e333c;
  }
  .td {
    flex: 1;
  }
  .td-2 {
    flex: 2;
  }
  .th .td,
  .tr .td {
    color: #637085;
    font-size: 14px;
    text-align: center;
  }
  .btn {
    border-radius: 3px;
    color: #fff;
    background-color: #638bd4;
    cursor: pointer;
    font-size: 14px;
    border: none;
    padding: 0 5px;
    line-height: 33px;
    text-align: center;
  }
  .no-data {
    color: #999;
    font-size: 14px;
    text-align: center;
    margin-top: 16px;
  }
}
</style>



